.header {
  padding-top: 44px;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }
  &__logo {
    width: 182px;
    margin-right: 40px;
    img {
      width: 100%;
    }
  }
  &__select {
    margin-right: 20px;
  }
  .nice-select {
    width: 102px;
    height: auto;
    background: transparent !important;
    border: none;
    padding: 0 15px 0 20px;
    font-size: 14px;
    line-height: 130%;
    font-weight: 400;
    white-space: unset;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 11px;
      height: 14px;
      background-image: url("../img/location.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 9px;
      height: 6px;
      background-image: url("../img/arrow-bottom.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    .list {
      width: 260px;
      display: grid;
      grid-template-columns: 100%;
      gap: 17px;
      background: #F0F4F7;
      border-radius: 6px;
      padding: 20px 30px;
      li {
        font-size: 15px;
        line-height: 130%;
        font-weight: 400 !important;
        background: transparent !important;
        border: none !important;
        padding: 0 !important;
        &:hover {
          color: #4272AC;
        }
      }
      .selected {
        text-decoration: underline;
        color: #4272AC;
      }
    }
  }
  .open {
    &:after {
      transform: translate(0, -50%) rotate(-180deg);
    }
  }
  &__search {
    display: flex;
    align-items: stretch;
    input {
      width: 177px;
      height: 35px;
      background: transparent;
      border: 1px solid transparent;
      padding: 0 10px;
      font-size: 16px;
      line-height: 130%;
      color: #2A2C2F;
      &::placeholder {
        color: rgba(42, 44, 47, 0.5);
      }
    }
    &-btn {
      width: 35px;
      height: 35px;
      flex-shrink: 0;
      background: transparent;
      padding: 5px;
      svg {
        max-width: 100%;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 9px;
  }
  &__btn {
    height: 35px;
    padding: 4px 20px;
    span {
      font-size: 16px;
    }
  }
  &__favorite {
    display: flex;
    align-items: center;
    gap: 5px;
    i {
      width: 20px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 16px;
      line-height: 130%;
      color: #1073E9;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 32px;
  }
  &__social {
    max-width: 26px;
    display: flex;
    align-items: center;
    svg {
      max-width: 100%;
      width: auto;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  .whatsapp {
    &:hover {
      svg path {
        fill: #00FF70;
      }
    }
  }
  .viber {
    &:hover {
      svg path {
        fill: #C07DEB;
      }
    }
  }
  .telegram {
    &:hover {
      svg .blue-path {
        fill: #56cafa;
      }
      svg .white-path {
        fill: #fff;
      }
    }
  }
  &__phones {
    display: flex;
    flex-direction: column;
    gap: 3px;
  }
  &__phone {
    display: block;
    font-size: 14px;
    line-height: 130%;
    &:hover {
      color: #767676;
    }
  }
  &__btn2 {
    height: 35px;
    border-radius: 6px;
    padding: 4px 20px;
    span {
      font-size: 14px;
    }
  }
}
.burger {
  position: relative;
  width: 40px;
  height: 18px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #2A272C;
    border-radius: 2px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
}
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 8px;
  padding: 32px 0 0 0;
  &-item__mob {
    display: none;
  }
  &-item__show {
    position: relative;
    display: block;
    font-size: 16px;
    line-height: 130%;
    padding-bottom: 28px;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      left: -10px;
      bottom: 18px;
      width: 0;
      height: 2px;
      background: #1073E9;
      transition: all 0.3s;
    }
    &:hover,
    &--active {
      color: #1073E9;
      &:after {
        width: calc(100% + 20px);
      }
    }
  }
  .sub-menu {
    position: fixed;
    z-index: 5;
    top: 160px;
    left: 0;
    transform: translateY(-5px);
    width: 100%;
    padding-left: max(120px,(100% - 1400px) / 2);
    padding-right: max(120px,(100% - 1400px) / 2);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &-item__list {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 15px 30px;
    background: #233C62;
    padding: 55px 65px;
  }
  &-subitem a {
    font-size: 15px;
    line-height: 120%;
    color: #fff;
    text-decoration: underline;
    text-decoration-color: transparent;
    &:hover {
      text-decoration-color: #27F891;
      color: #27F891;
    }
  }
  &-item--active {
    .menu-item__show {
      color: #1073E9;
      &:after {
        width: calc(100% + 20px);
      }
    }
    .sub-menu {
      transform: translateY(0);
      opacity: 1;
      pointer-events: auto;
    }
  }
}
.header-mob {
  position: fixed;
  z-index: 10;
  top: 0;
  right: -105%;
  width: 400px;
  height: 100%;
  transition: all 0.5s;
  &--active {
    right: 0;
    .header-mob__bg {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(4, 25, 40, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: all 0.125s;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
    background: #fff;
    padding: 30px 30px 60px 30px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  .nice-select {
    width: 140px;
    font-size: 18px;
  }
  &__close {
    width: 15px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        fill: #2A2C2F;
      }
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  .header__search {
    position: relative;
    margin-top: 60px;
    input {
      width: 100%;
      height: 50px;
      display: flex;
      border: 2px solid #EBEFF2;
      border-radius: 6px;
      padding: 0 40px 0 16px;
      font-size: 24px;
    }
    &-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 16px;
      transform: translate(0, -50%);
      width: 22px;
      height: auto;
      padding: 0;
    }
  }
  .menu {
    display: flex !important;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    gap: 45px;
    padding: 0;
    margin-top: 60px;
    &-item {
      display: block !important;
    }
    &-item__show {
      padding-bottom: 0;
      font-size: 24px;
      &:after {
        display: none;
      }
    }
    .sub-menu {
      position: static;
      transform: translateY(0);
      display: none;
      padding: 0;
      opacity: 1;
      pointer-events: auto;
    }
    &-item__list {
      grid-template-columns: 100%;
      gap: 30px;
      background: transparent;
      padding: 45px 0 0 20px;
    }
    &-subitem a {
      font-size: 19px;
      color: #4272AC;
      &:hover {
        color: #01DB7D;
      }
    }
  }
  .header__logo {
    position: relative;
    width: 160px;
    display: block;
    padding: 35px 0;
    margin: 60px 0 0 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -30px;
      width: 400px;
      height: 100%;
      background: #F0F4F7;
    }
    img {
      position: relative;
      z-index: 1;
    }
  }
  .header__socials {
    gap: 40px;
    margin-top: 60px;
  }
  .header__social {
    width: 40px;
    max-width: 100%;
    svg {
      width: 100%;
      height: auto;
    }
  }
  .header__phones {
    position: relative;
    display: flex !important;
    padding-left: 30px;
    margin-top: 45px;
    &:after {
      content: '';
      position: absolute;
      top: 3px;
      left: 0;
      width: 18px;
      height: 18px;
      background-image: url("../img/phone.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
  .header__phone {
    font-size: 20px;
  }
  &__btn {
    height: 50px;
    padding: 4px 32px;
    margin-top: 30px;
    span {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .header {
    padding-top: 25px;
    &__wrap {
      gap: 15px;
    }
    &__right {
      flex-grow: unset;
      gap: 20px;
    }
    &__logo {
      width: 150px;
      margin-right: 30px;
    }
    &__search {
      input {
        width: 140px;
      }
    }
    &__btn {
      padding: 4px 16px;
      span {
        font-size: 15px;
      }
    }
    &__socials {
      gap: 20px;
    }
    &__phone {
      font-size: 13px;
    }
    &__btn2 {
      padding: 4px 14px;
      span {
        font-size: 13px;
      }
    }
  }
  .menu {
    padding: 24px 0 0 0;
    &-item__show {
      padding-bottom: 24px;
      &:after {
        bottom: 14px;
      }
    }
    .sub-menu {
      top: 126px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .menu {
    &-item__show {
      font-size: 15px;
      &:hover,
      &--active {
        &:after {
          width: calc(100% + 10px);
        }
      }
      &:after {
        left: -5px;
      }
    }
    &-item--active {
      .menu-item__show {
        &:after {
          width: calc(100% + 10px);
        }
      }
    }
  }
}

@media screen and (max-width: 1360px) {
  .menu {
    .sub-menu {
      padding-left: max(40px,(100% - 1400px) / 2);
      padding-right: max(40px,(100% - 1400px) / 2);
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__search {
      input {
        display: none;
      }
    }
    &__phones {
      display: none;
    }
  }
  .menu {
    &-item__show {
      font-size: 14px;
    }
    .sub-menu {
      top: 122px;
    }
    &-item__list {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    }
  }
}
@media screen and (max-width: 992px) {
  .header {
    &__btn2 {
      display: none;
    }
  }
  .menu {
    &-item {
      &:nth-child(7) ~ .menu-item {
        display: none;
      }
    }
    &-item__show {
      font-size: 16px;
    }
    &-item__mob {
      display: block;
      .sub-menu {
        left: unset;
        right: 0;
        width: auto;
      }
      .menu-item__list {
        grid-template-columns: 100%;
        padding: 20px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .header {
    padding: 20px 0;
    &__left {
      .header__select,
      .header__search {
        display: none;
      }
    }
    &__right {
      .header__favorite,
      .header__socials {
        display: none;
      }
    }
    &__logo {
      width: 120px;
    }
    &__btns {
      gap: 6px;
    }
    &__btn {
      padding: 4px 16px;
    }
  }
  .burger {
    display: block;
  }
  .menu {
    display: none;
  }
}

@media screen and (max-width: 450px) {
  .header {
    &__btns {
      display: none;
    }
  }
  .header-mob {
    width: 100%;
    &__bg {
      display: none;
    }
    .header__logo {
      &:after {
        width: 450px;
      }
    }
  }
}