.wrapper {
  overflow: hidden;
}
.btn-blue {
  background: #1073E9;
  border-radius: 6px;
  &:hover {
    background: #31A8FF;
  }
  span {
    line-height: 130%;
    font-weight: 500;
    color: #fff;
  }
}
.btn-blue__outline {
  background: transparent;
  border: 1px solid #1073E9;
  border-radius: 6px;
  color: #1073E9;
  &:hover {
    border-color: #31A8FF;
    color: #31A8FF;
  }
  span {
    line-height: 130%;
    font-weight: 500;
  }
}
.btn-gray {
  background: #E2E8EF;
  color: #2A2C2F;
  &:hover {
    background: #F0F4F7;
    color: #767676;
  }
  span {
    line-height: 130%;
  }
}
.btn-green {
  background: linear-gradient(0.00deg, rgb(0, 145, 58) 5.833%,rgb(0, 225, 131) 100%);
  background-color: #01E183;
  border-radius: 6px;
  &:hover {
    background: #01E183;
  }
  span {
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
}
.h1 {
  font-size: 48px;
  line-height: 110%;
  font-weight: 700;
}
.h2 {
  font-size: 36px;
  line-height: 110%;
  font-weight: 500;
}
.h3 {
  font-size: 24px;
  line-height: 110%;
  font-weight: 500;
}
.nice-select {
  display: flex;
  align-items: center;
  background: #fff !important;
  border: none;
  padding: 0 20px 0 10px;
  font-size: 16px;
  line-height: 130%;
  font-weight: 400;
  white-space: unset;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    right: 14px;
    transform: translate(0, -50%);
    width: 9px;
    height: 6px;
    background-image: url("../img/arrow-bottom.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }
  .list {
    z-index: 3;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 8px;
    background: #fff;
    box-shadow: unset;
    border-radius: 0 0 6px 6px;
    padding: 4px 10px 26px 10px;
    margin-top: 0;
    li {
      font-size: 16px;
      line-height: 130%;
      font-weight: 400 !important;
      background: transparent !important;
      border: none !important;
      padding: 0 !important;
      &:hover {
        color: #4272AC;
      }
    }
    .selected {
      text-decoration: underline;
      color: #4272AC;
    }
  }
}
.open {
  border-radius: 6px 6px 0 0;
  &:after {
    transform: translate(0, -50%) rotate(-180deg) !important;
  }
}
.swiper-button-prev,
.swiper-button-next {
  border-radius: 50%;
  &:after {
    display: none;
  }
  svg {
    max-width: 90%;
  }
}
.swiper-button-disabled {
  opacity: 0.5 !important;
}

@media screen and (max-width: 1400px) {
  .h1 {
    font-size: 36px;
  }
}

@media screen and (max-width: 576px) {
  .h1 {
    font-size: 28px;
  }
}

@media screen and (max-width: 359px) {
  .h2 {
    font-size: 28px;
  }
  .h3 {
    font-size: 20px;
  }
}