.main {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 100px 0 200px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180.00deg, rgb(0, 23, 69) 33.333%,rgba(3, 0, 153, 0) 70.75%);
    opacity: 0.3;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    max-width: 600px;
    color: #fff;
  }
  &-form {
    background: rgb(16, 51, 94, 0.5);
    border-radius: 6px;
    padding: 30px;
    margin-top: 10px;
  }
  &-form__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &-form__group {
    display: flex;
    align-items: center;
    gap: 4px;
    &:nth-child(1) {
      flex-grow: 1;
    }
  }
  .nice-select {
    width: 175px;
    height: 40px;
  }
  &-form__input {
    position: relative;
    flex-grow: 1;
    &-wrap {
      height: 40px;
      flex-grow: 1;
      display: flex;
      align-items: stretch;
      background: #fff;
      border-radius: 6px;
      padding: 0 10px;
      label {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 130%;
        margin-left: 10px;
        span {
          background: #C8E9F8;
          border-radius: 20px;
          padding: 4px 8px;
        }
      }
    }
    input {
      width: 100%;
      height: 100%;
      background: transparent;
      border: 1px solid transparent;
      padding: 0 20px 0 0;
      font-size: 16px;
      line-height: 130%;
      color: #2A2C2F;
      &::placeholder {
        color: #959697;
      }
    }
    .input-text {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      font-size: 16px;
      line-height: 130%;

    }
  }
  &-form__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px 10px;
    margin-top: 20px;
  }
  &-form__checkboxes {
    display: flex;
    align-items: stretch;
    gap: 4px;
  }
  &-form__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: #5894DC;
      border-color: #5894DC;
    }
    label {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #4272AC;
      border: 2px solid #4272AC;
      border-radius: 30px;
      padding: 6px 20px;
      font-size: 16px;
      line-height: 130%;
      color: #fff;
      transition: all 0.3s;
    }
  }
  &-form__more {
    flex-shrink: 0;
    background: #fff;
    border: 2px solid #0074FF;
    border-radius: 30px;
    padding: 6px 20px;
    font-size: 16px;
    line-height: 130%;
    color: #0074FF;
  }
  &-form__btns {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }
  &-form__btn {
    height: 60px;
    padding: 5px 40px;
    span {
      font-size: 18px;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  .btn-blue {
    background: linear-gradient(180.00deg, rgb(0, 116, 255),rgb(0, 71, 177) 100%);
    background-color: #0074FF;
    &:hover {
      background: #0074FF;
    }
  }
}
.offers {
  padding: 120px 0 140px;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  &-prev {
    left: -150px;
    width: 70px;
    height: 70px;
    background: #EFF2F5;
  }
  &-next {
    right: -150px;
    width: 70px;
    height: 70px;
    background: #EFF2F5;
  }
  &__btn {
    width: 100%;
    height: 50px;
    display: none;
    background: #CBD4E1;
    margin-top: 30px;
    span {
      font-size: 16px;
    }
  }
}
.offer-card {
  height: auto;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 2px solid #EBEFF2;
  border-radius: 6px;
  transition: all 0.3s;
  &:hover {
    background: #F0F4F7;
    .offer-card__title {
      color: #4272AC;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 68%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 6px 6px 0 0;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 20px 15px;
  }
  .favorite {
    position: absolute;
    z-index: 2;
    top: 12px;
    right: 12px;
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border-radius: 50%;
    cursor: pointer;
    &--active {
      svg path {
        fill: #FF0000;
        stroke: #FF0000;
      }
    }
    svg {
      width: 16px;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__title {
    font-size: 18px;
    line-height: 110%;
    font-weight: 500;
  }
  &__location {
    font-size: 15px;
    line-height: 120%;
    margin-top: 30px;
    margin-bottom: auto;
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    margin-top: 16px;
  }
  &__price {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
  }
  &__size {
    font-size: 16px;
    line-height: 130%;
    color: #4272AC;
  }
  &__group2 {
    margin-top: 12px;
  }
  &__area {
    font-size: 15px;
    line-height: 120%;
  }
  &__metro {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 30px;
    i {
      width: 22px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 15px;
      line-height: 120%;
    }
  }
  &__slider {
    position: relative;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 34px;
    height: 34px;
    background: #EFF2F5;
  }
  .swiper-pagination {
    &-bullet {
      background: rgba(0, 0, 0, 0.5);
      &-active {
        background: #fff;
      }
    }
  }
}
.offer-card--row {
  &:hover {
    background: #fff;
  }
  @media screen and (min-width: 768px) {
    flex-direction: row;
    align-items: stretch;
    .offer-card__slider,
    .offer-card__img,
    .offer-card__desc {
      width: 50%;
    }
    .offer-card__desc {
      justify-content: center;
      padding: 20px;
    }
    .offer-card__img {
      padding-bottom: 38%;
      img {
        border-radius: 6px 0 0 6px;
      }
    }
    .offer-card__slider {
      .swiper,
      .swiper-wrapper,
      .swiper-slide,
      .offer-card__img {
        height: 100%;
      }
      .offer-card__img {
        width: 100%;
        padding-bottom: 76%;
      }
    }
    .favorite {
      left: 12px;
      right: unset;
    }
    .offer-card__location {
      margin-bottom: 0;
    }
  }
}
.catalog {
  position: relative;
  z-index: 2;
  background: #F0F4F7;
  padding: 60px 0 120px;
  &__tabs-head {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 4px;
    margin-top: 30px;
  }
  &__tab {
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #4272AC;
    border-radius: 6px;
    padding: 4px 20px;
    font-size: 16px;
    line-height: 130%;
    color: #4272AC;
    &:hover,
    &--active {
      background: #4272AC;
      color: #fff;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
    margin-top: 60px;
  }
  &__btn {
    height: 35px;
    display: inline-flex;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 6px;
    padding: 4px 20px;
    margin-top: 60px;
    &:hover {
      border-color: rgba(66, 114, 172, 0.30);
      color: #4272AC;
    }
    span {
      font-size: 14px;
      line-height: 130%;
    }
  }
}
.banner {
  background: #2E4467;
  color: #fff;
  &__inner {
    position: relative;
    padding: 64px 0;
    &:before {
      content: '';
      position: absolute;
      top: 45px;
      right: 33px;
      width: 53px;
      height: 53px;
      background: rgba(242, 90, 255, 0.2);
      filter: blur(12px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__desc {
    position: relative;
    z-index: 4;
    max-width: 625px;
    .banner__logo {
      display: none;
    }
  }
  &__title {
    font-size: 48px;
    text-transform: uppercase;
  }
  &__text {
    font-size: 20px;
    line-height: 130%;
    font-weight: 500;
    margin-top: 15px;
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    margin-top: 48px;
    span {
      font-size: 16px;
    }
  }
  &__img-wrap {
    position: absolute;
    right: 138px;
    bottom: 0;
    max-width: 476px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 600px;
      height: 600px;
      background: rgb(16, 115, 233);
      filter: blur(300px);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 3;
      left: -113px;
      bottom: -63px;
      width: 163px;
      height: 163px;
      background: rgba(21, 216, 216, 0.23);
      filter: blur(2px);
      backdrop-filter: blur(2px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__img {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: -65px;
      bottom: 40px;
      width: 200px;
      height: 200px;
      background: rgba(90, 255, 215, 0.2);
      filter: blur(12px);
      border-radius: 50%;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: -87px;
      bottom: 30px;
      width: 220px;
      height: 220px;
      background: rgba(21, 216, 99, 0.93);
      filter: blur(50px);
      border-radius: 50%;
      opacity: 0.6;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 2;
      width: 100%;
      display: block;
    }
  }
  &__logo {
    position: absolute;
    z-index: 3;
    right: -82px;
    bottom: 74px;
    &:after {
     content: '';
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     width: 320px;
     height: 320px;
     background: rgba(21, 216, 216, 0.2);
     filter: blur(10px);
     backdrop-filter: blur(2px);
     border-radius: 50%;
     pointer-events: none;
   }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: -116px;
      left: -88px;
      width: 313px;
      height: 313px;
      background: rgba(189, 21, 216, 0.93);
      filter: blur(60px);
      border-radius: 50%;
      opacity: 0.6;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 254px;
    }
  }
}
.services {
  position: relative;
  z-index: 5;
  background: #fff;
  padding: 140px 0 70px;
  &__inner {
    background: #F0F4F7;
    padding: 55px 40px 40px 40px;
  }
  &__title {
    text-align: center;
    text-transform: uppercase;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 60px;
  }
  &__item {
    min-height: 68px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #4272AC;
    border-radius: 6px;
    padding: 10px;
    &:hover {
      background: #1BC170;
    }
    span {
      font-size: 15px;
      line-height: 100%;
      font-weight: 500;
      color: #fff;
    }
  }
}
.recommendations {
  position: relative;
  z-index: 2;
  background: #fff;
  padding: 70px 0 140px;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 50px;
  }
}
.recommendation-card {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 20px;
  background: #fff;
  border: 2px solid #EBEFF2;
  border-radius: 6px;
  padding: 20px;
  &:hover {
    background: #F1F4F7;
  }
  .gallery {
    position: relative;
    width: 490px;
    display: flex;
    align-items: stretch;
    gap: 3px;
    padding-bottom: 23.5%;
    .swiper {
      height: 100%;
    }
  }
  .gallery__big {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 110px);
    height: 100%;
  }
  .gallery__small {
    position: absolute;
    top: 0;
    left: 0;
    width: 107px;
    height: 100%;
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      border-radius: 6px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 34px;
    height: 34px;
    background: #EFF2F5;
  }
  &__desc {
    width: calc(100% - 510px);
    display: flex;
    justify-content: space-between;
    gap: 20px;
    &-left {
      width: calc(100% - 280px);
    }
    &-right {
      width: 260px;
      flex-shrink: 0;
    }
  }
  &__title {
    font-size: 22px;
    line-height: 110%;
    font-weight: 500;
  }
  &__group {
    margin-top: 40px;
  }
  &__location {
    font-size: 15px;
    line-height: 120%;
  }
  &__metro-wrap {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 12px;
  }
  &__metro {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      width: 22px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 15px;
      line-height: 120%;
    }
  }
  &__time {
    position: relative;
    font-size: 15px;
    line-height: 120%;
    color: #4272AC;
    padding-left: 12px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 16px;
      background: #959697;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px 32px;
    margin-top: 40px;
    li {
      position: relative;
      font-size: 15px;
      line-height: 120%;
      &:last-child {
        &:after {
          display: none;
        }
      }
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -15px;
        transform: translate(0, -50%);
        width: 1px;
        height: 16px;
        background: #959697;
      }
      strong {
        font-weight: 700;
      }
    }
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 5px;
    margin-top: 16px;
  }
  &__info-item {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    padding: 5px 20px;
    font-size: 15px;
    line-height: 120%;
  }
  &__text {
    margin-top: 40px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
    p {
      font-size: 15px;
      line-height: 120%;
    }
  }
  &__price {
    font-size: 26px;
    line-height: 130%;
    font-weight: 700;
  }
  &__size {
    font-size: 16px;
    line-height: 130%;
    color: #4272AC;
    margin-top: 8px;
  }
  .favorite {
    display: flex;
    align-items: center;
    gap: 15px;
    cursor: pointer;
    margin-top: 16px;
    &--active {
      .favorite-icon {
        svg path {
          fill: #FF0000;
          stroke: #FF0000;
        }
      }
    }
    &-icon {
      width: 44px;
      height: 44px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EFF2F5;
      border-radius: 50%;
      svg {
        width: 18px;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    &-text {
      font-size: 15px;
      line-height: 120%;
    }
  }
  &__message-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px 4px;
    margin-top: 30px;
  }
  &__message-user {
    font-size: 15px;
    line-height: 120%;
    font-weight: 700;
  }
  &__message {
    border-color: #4272AC;
    border-radius: 20px;
    padding: 4px 10px;
    color: #4272AC;
    &:hover {
      background: #fff;
      border-color: #C6D5E6;
      color: #4272AC;
    }
    span {
      font-size: 13px;
    }
  }
  &__phones {
    display: inline-flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 12px;
  }
  &__phone {
    font-size: 15px;
    line-height: 120%;
    color: #4272AC;
  }
  &__btn {
    width: 100%;
    height: 42px;
    padding: 5px 40px;
    margin-top: 20px;
    span {
      font-size: 14px;
    }
  }
}



@media screen and (max-width: 1800px) {
  .offers {
    &-prev {
      left: -96px;
    }
    &-next {
      right: -96px;
    }
  }
}

@media screen and (max-width: 1650px) {
  .main {
    .nice-select {
      width: 140px;
    }
    &-form__checkbox {
      label {
        padding: 7px 13px;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .main {
    &-form {
      padding: 20px 24px;
    }
    &-form__input {
      input {
        font-size: 14px;
      }
      .input-text {
        font-size: 14px;
      }
      &-wrap {
        label {
          font-size: 14px;
        }
      }
    }
    .nice-select {
      font-size: 14px;
      .list {
        li {
          font-size: 14px;
        }
      }
    }
    &-form__checkbox {
      label {
        font-size: 13px;
      }
    }
    &-form__more {
      padding: 7px 13px;
      font-size: 14px;
    }
    &-form__btn {
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
  }
  .offer-card {
    @media screen and (min-width: 577px) {
      &__desc {
        padding: 20px 10px;
      }
      &__title {
        font-size: 15px;
      }
      &__location {
        font-size: 13px;
      }
      &__price {
        font-size: 19px;
      }
      &__size {
        font-size: 15px;
      }
      &__metro {
        margin-top: 18px;
      }
    }
  }
  .offer-card--row {
    .offer-card__desc {
      padding: 20px 15px;
    }
  }
  .banner {
    &__img-wrap {
      right: 60px;
    }
    &__logo {
      right: -60px;
    }
  }
  .recommendation-card {
    .gallery {
      width: 400px;
    }
    .gallery__big {
      width: calc(100% - 81px);
    }
    .gallery__small {
      width: 78px;
    }
    &__desc {
      width: calc(100% - 420px);
    }
    &__group {
      margin-top: 30px;
    }
    &__text {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    padding: 75px 0 165px;
    &__title {
      max-width: 500px;
    }
  }
  .offers {
    padding: 120px 0;
    &__title {
      padding-right: 190px;
    }
    &__slider {
      margin-top: 40px;
    }
    &-prev {
      top: -95px;
      left: unset;
      right: 110px;
      margin-top: 0;
    }
    &-next {
      top: -95px;
      right: 0;
      margin-top: 0;
    }
  }
  .services {
    padding: 120px 0 60px;
  }
  .recommendations {
    padding: 60px 0 120px;
  }
}

@media screen and (max-width: 1360px) {
  .main {
    padding-bottom: 100px;
    &-form {
